					<div class="row nomargin" style="border-bottom: 0.05em solid #ccc;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">累計購物返利：{{userInfo.returnCoins}} R幣</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">返利明細</a>
						</div>
					</div>
					<div class="row nomargin" style="margin-top: 1em;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">R幣餘額：{{userInfo.Rcoins}} R幣</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">交易明細</a>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;margin-right: 3em;">儲值R幣</a>
						</div>
					</div>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;border-bottom: 0.5em solid #f5f5f5;">
				<div class="col-sm-12 col-xs-12">
					<div class="row nomargin" style="border-bottom: 0.05em solid #ccc;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">我的銀行賬戶：{{bankNum}}</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">添加賬戶</a>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;margin-right: 3em;">查看明細</a>
						</div>
					</div>
					<div class="row nomargin" style="margin-top: 1em;border-bottom: 0.05em solid #ccc;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">我的支付寶賬戶：{{zhifubaoNum}}</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">添加賬戶</a>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;margin-right: 3em;">查看明細</a>
						</div>
					</div>
					<div class="row nomargin" style="margin-top: 1em;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">我的微信賬戶：{{wechatNum}}</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">添加賬戶</a>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;margin-right: 3em;">查看明細</a>
						</div>
					</div>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;border-bottom: 0.5em solid #f5f5f5;margin-bottom: 6em;">
				<div class="col-sm-12 col-xs-12">
					<div class="row nomargin" style="border-bottom: 0.05em solid #ccc;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">我的訂單</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">查看明細</a>
						</div>
					</div>
					<div class="row nomargin" style="margin-top: 1em;">
						<div class="col-sm-12 col-xs-12 nopadding">
							<p class="pull-left">我的提問</p>
							<a href="javascript:void(0)" class="pull-right" style="color: #2a6baf;">查看明細</a>
						</div>
					</div>
				</div>
			</div>
			<div class="row nomargin" style="position: fixed;bottom: 0;background: #fff;width: 100%;border-top: 0.05em solid #ccc;padding-top: 0.5em;">
				<a href="">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/shouyeicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;">首頁</p></center>
					</div>
				</a>
				<a href="">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/daifuicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;">代付</p></center>
					</div>
				</a>
				<a href="/mobileEntry">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/daigouicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;">代充</p></center>
					</div>
				</a>
				<a href="">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/huiyuanicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;">會員中心</p></center>
					</div>
				</a>
			</div>
		</div>
	</div>
</template>

<script>
	import {getService} from '../../assets/js/configs.js'
//	import {checkLogin} from '../../assets/js/checkLogin.js'
	export default {
	  name: 'cen',
	  data(){
	  	return{
	  		userInfo:'',
	  		bankNum:'',
	  		zhifubaoNum:'',
	  		wechatNum:''
	  	}
	  },
	  mounted:function (){
	  	this.getInfo();
//	  	checkLogin();
	  },
	  methods:{
	  	getInfo:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/user/getInfo",
	  			dataType: "json",
	  			data:{
	  				
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.userInfo = res.data;
			    		this.bankNum = this.userInfo.bankAccounts.length;
			    		this.zhifubaoNum = this.userInfo.aliPayAccounts.length;
			    		this.wechatNum = this.userInfo.wechatAccounts.length;
			    	}
			    }.bind(this)
	  		});
	  	}
	  }
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
</style>


